*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

#root, #__next {
  isolation: isolate;
}

/* Main */

body {
  font-family: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  background-color: var(--dark);
  color: var(--light);
  
  --dark: #0B0B0B;
  --light: #e0e0e0;
  --purple: #9747FF;
  --grey: #1B1B1B;
  --darker: rgba(0, 0, 0, 0.2);
  --darker-grey: #171717;
}

.container {
  display: grid;
  grid-template-columns: minmax(auto, 1fr) 4fr;
  grid-template-rows: auto 1fr;
  height: 100dvh;
}

.nav {
  display: grid;
  background-color: var(--grey);
  align-content: start;
  grid-template-rows: 100px 1fr auto;
  grid-row: 1 / 3;
} 

.nav > * {
  padding: 0 20px;
}

.profile {
  display: flex;
  align-items: center;
  font-size: 2rem;
  font-weight: 600;
}

.profile svg {
  flex: none;
  position: relative;
  top: 2px;
  height: 54px;
  fill: var(--light);
  margin-right: 18px;
}

.projects > p {
  font-size: 1.6rem;
  font-weight: 600;
  margin-bottom: 8px;
}

.tabs {
  display: grid;
  grid-auto-rows: 40px;
  gap: 4px;
  white-space: nowrap;
  overflow: hidden;
}


.tab, .createProject {
  border: none;
  display: flex;
  padding: 4px 0 4px 24px;
  gap: 8px;
  align-items: center;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: 500;
  color: var(--light);
  background-color: transparent;
  cursor: pointer;
}

.tab {
  position: relative;
}


.tab.active {
  background-color: var(--darker);
  color: var(--purple);
}

.tab:hover, .createProject:hover {
  background-color: var(--darker);
}

.tab .deleteProjectBtn {
  position: absolute;
  right: 4px;
  height: 85%;
  background-color: transparent;
  z-index: 1000;
  border: none;
  padding: 2px;
  border-radius: 5px;
  cursor: pointer;
}

.tab .deleteProjectBtn:hover svg {
  fill: var(--purple);
}

.tab svg {
  pointer-events: none;
  flex: none;
  height: 100%;
  fill: var(--light);
}

.tab input {
  min-width: 0;
  width: 100%;
  background-color: transparent;
  border: none;
  color: var(--light);
}

.tab input:focus {
  outline: none;
}

.createProject svg {
  pointer-events: none;
  flex: none;
  height: 100%;
  fill: var(--purple);
}

.nav footer {
  font-size: 1.2rem;
  background-color: var(--darker);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
}

header {
  font-size: 2.7rem;
  padding: 12px 100px;
  border-bottom: 4px solid var(--purple);
  grid-row: 1 / 2;
  font-weight: 500;
}

.projectTasks {
  padding: 25px 100px;
  display: grid;
  gap: 16px;
  overflow: auto;
  grid-auto-rows: 70px;
}


.task {
  display: flex;
  align-items: center;
  padding: 16px 20px;
  font-size: 1.5rem;
  background-color: var(--grey);
  border-radius: 16px;
  gap: 12px;
}


.task:hover {
  background-color: var(--darker-grey);
}


.task .taskTitle {
  flex: 1;
  white-space: nowrap;
  overflow: auto;
  scrollbar-color: var(--light) var(--grey);
  scrollbar-width: thin;
  min-width: 50px;
}

.checkbox input {
  appearance: none;
  position: relative;
  width: 30px;
  height: 30px;
  background: transparent;
  box-shadow: inset 0 0 5px rgb(0 0 0 / 0.2);
  border-radius: 50%;
  border: 2px solid var(--purple);
  transition: 500ms;
  cursor: pointer;
}

.checkbox input::after {
  content: "";
  position: absolute;
  left: -1.11px;
  width: 0px;
  height: 0px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  transition: 500ms;
}

.checkbox input:checked::after {
  width: 30px;
  height: 30px;
}

.checkbox input:checked {
  background-color: var(--purple);
}

.task .btns {
  flex: none;
  display: flex;
  height: 130%;
  margin-left: 10%;
  gap: 8px;
}

.task button {
  padding: 4px;
  border-radius: 8px;
  border: none;
  background-color: transparent;
}

.task button:hover {
  cursor: pointer;
  background-color: var(--darker);
}

.task .btns svg {
  pointer-events: none;
  height: 100%;
  flex: none;
  fill: var(--purple);
}

#addTask {
  background-color: var(--dark);
  display: flex;
  border-radius: 16px;
  background-color: var(--grey);
  filter: brightness(0.7);
  border: none;
}

#addTask:hover {
  filter: brightness(0.9);
  cursor: pointer;
}

#addTask svg {
  pointer-events: none;
  height: 50px;
  fill: var(--purple);
  margin: auto;
  filter: brightness(0.6);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(2px);
  background-color: var(--darker);
  display: flex;
  z-index: 1000;
}

.taskForm {
  margin: auto;
  background-color: var(--grey);
  height: 75%;
  width: 28%;
  min-width: 320px;
  border-radius: 28px;
  padding: 1%;
  display: flex;
  flex-direction: column;
}

.taskForm .close {
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-radius: 8%;
  align-self: end;
  padding: 2px;
  height: 48px;
}

.taskForm .close:hover {
  background-color: var(--darker);
}

.taskForm .close svg {
  pointer-events: none;
  height: 100%;
  fill: var(--purple);
}

.taskForm h2 {
  text-align: center;
  font-size: 2rem;
  margin: 12px 0 20px;
}

.taskForm .fields {
  padding: 0 20px;
}

.taskForm p {
  display: flex;
  gap: 12px;
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.taskForm p input,
.taskForm p select {
  min-width: 0;
  font-size: 1.3rem;
  height: 2em;
  flex: 1;
  padding: 0 8px;
  background-color: var(--dark);
  border: none;
  border-radius: 12px;
  color: var(--light);
}

.taskForm p input:focus,
.taskForm p select:focus {
  outline: 2px solid var(--purple);
}

.taskForm .formSubmitBtn {
  margin: auto auto 12px;
  font-size: 1.5rem;
  font-weight: 500;
  background-color: transparent;
  border: 4px solid var(--purple);
  border-radius: 16px;
  color: var(--purple);
  padding: 4px 28px;
  cursor: pointer;
}

.taskForm .formSubmitBtn:hover {
  background-color: var(--darker);
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,cAAc;;AAEd;EACE,sBAAsB;AACxB;;AAEA;EACE,SAAS;EACT,UAAU;AACZ;;AAEA;EACE;IACE,gCAAgC;EAClC;AACF;;AAEA;EACE,gBAAgB;EAChB,mCAAmC;AACrC;;AAEA;EACE,cAAc;EACd,eAAe;AACjB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,iBAAiB;AACnB;AACA;EACE,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;AACpB;;AAEA,SAAS;;AAET;EACE,kIAAkI;EAClI,6BAA6B;EAC7B,mBAAmB;;EAEnB,eAAe;EACf,gBAAgB;EAChB,iBAAiB;EACjB,eAAe;EACf,4BAA4B;EAC5B,sBAAsB;AACxB;;AAEA;EACE,aAAa;EACb,4CAA4C;EAC5C,4BAA4B;EAC5B,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,6BAA6B;EAC7B,oBAAoB;EACpB,kCAAkC;EAClC,eAAe;AACjB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,UAAU;EACV,kBAAkB;EAClB,QAAQ;EACR,YAAY;EACZ,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,oBAAoB;EACpB,QAAQ;EACR,mBAAmB;EACnB,gBAAgB;AAClB;;;AAGA;EACE,YAAY;EACZ,aAAa;EACb,uBAAuB;EACvB,QAAQ;EACR,mBAAmB;EACnB,kBAAkB;EAClB,iBAAiB;EACjB,gBAAgB;EAChB,mBAAmB;EACnB,6BAA6B;EAC7B,eAAe;AACjB;;AAEA;EACE,kBAAkB;AACpB;;;AAGA;EACE,+BAA+B;EAC/B,oBAAoB;AACtB;;AAEA;EACE,+BAA+B;AACjC;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,WAAW;EACX,6BAA6B;EAC7B,aAAa;EACb,YAAY;EACZ,YAAY;EACZ,kBAAkB;EAClB,eAAe;AACjB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,oBAAoB;EACpB,UAAU;EACV,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,WAAW;EACX,6BAA6B;EAC7B,YAAY;EACZ,mBAAmB;AACrB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,oBAAoB;EACpB,UAAU;EACV,YAAY;EACZ,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;EACjB,+BAA+B;EAC/B,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,YAAY;AACd;;AAEA;EACE,iBAAiB;EACjB,mBAAmB;EACnB,sCAAsC;EACtC,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,mBAAmB;EACnB,aAAa;EACb,SAAS;EACT,cAAc;EACd,oBAAoB;AACtB;;;AAGA;EACE,aAAa;EACb,mBAAmB;EACnB,kBAAkB;EAClB,iBAAiB;EACjB,6BAA6B;EAC7B,mBAAmB;EACnB,SAAS;AACX;;;AAGA;EACE,oCAAoC;AACtC;;;AAGA;EACE,OAAO;EACP,mBAAmB;EACnB,cAAc;EACd,yCAAyC;EACzC,qBAAqB;EACrB,eAAe;AACjB;;AAEA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,uBAAuB;EACvB,0CAA0C;EAC1C,kBAAkB;EAClB,